.minicart {
    position: absolute;
    top: 110%;
    right: 0;
    background-color: $color-white;
    width: 20rem;
    height: fit-content;
    min-height: 3rem;
    padding: 1rem;
    border-radius: 5px;
    opacity: 0;
    z-index: -1;
    box-shadow: $box-shadow;
    transition: opacity $transition-rules;

    &__info {
        span {
            display: flex;
            font-size: 1.4rem;
            line-height: 1.6rem;
            color: $color-black;

            span {
                font-style: italic;
                color: $color-maroon;
            }
        }
    }

    @media(min-width: $tablet-width) {
        &.js-minicart-showed {
            animation: showMiniCart 0.6s forwards;
        }
    
        @keyframes showMiniCart {
            0% {
                opacity: 0;
                z-index: -1;
            }
    
            100% {
                opacity: 1;
                z-index: 100;
            }
        }
    }
}