p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  text-decoration: none; }

input, textarea {
  border-radius: 0; }

svg {
  fill: #ffffff; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/*.zoomist-container {
  height: 600px;
} */
.button-default, .product-card__content .product-card__chars .product-card__item .product-card__item-btn, .product-card__content .product-card__button-group .product-card__btn, .counter button {
  background-color: transparent;
  border: 0;
  padding: 0; }

.flex-centered-content, .counter button, .counter span {
  display: flex;
  align-items: center;
  justify-content: center; }

.cmn__wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1640px;
  min-width: 320px; }

.cmn__container {
  padding: 0 10rem; }
  @media (max-width: 1140px) {
    .cmn__container {
      padding: 0 2rem; } }

.cmn__title {
  font-weight: 500;
  font-size: 5rem;
  line-height: 6rem;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #000000; }
  @media (max-width: 1140px) {
    .cmn__title {
      font-size: 3.6rem;
      line-height: 5.4rem; } }
  @media (max-width: 768px) {
    .cmn__title {
      font-size: 2.8rem;
      line-height: 4.2rem; } }
  .cmn__title--white {
    color: #ffffff; }
  .cmn__title--med {
    font-size: 3.6rem;
    line-height: 4.4rem;
    letter-spacing: 0.02em; }
    @media (max-width: 1140px) {
      .cmn__title--med {
        font-size: 2.4rem;
        line-height: 3rem; } }
    @media (max-width: 768px) {
      .cmn__title--med {
        font-size: 1.8rem;
        line-height: 2.4rem; } }
  .cmn__title--sm {
    font-size: 1.8rem;
    line-height: 2.5rem; }
    @media (max-width: 1140px) {
      .cmn__title--sm {
        font-size: 1.6rem;
        line-height: 2.2rem; } }
    @media (max-width: 768px) {
      .cmn__title--sm {
        font-size: 1.4rem;
        line-height: 2rem; } }

.cmn__text {
  font-size: 2rem;
  line-height: 3rem;
  color: #000000; }
  .cmn__text--white {
    color: #ffffff; }
  @media (max-width: 1140px) {
    .cmn__text {
      font-size: 1.7rem;
      line-height: 2.4rem; } }
  @media (max-width: 768px) {
    .cmn__text {
      font-size: 1.4rem;
      line-height: 1.8rem; } }

.cmn__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 1.8rem;
  line-height: 2.2rem;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  width: fit-content;
  padding: 1rem 3rem;
  color: #ffffff;
  border: 0.3rem solid #ffffff; }
  @media (min-width: 768px) {
    .cmn__btn {
      transition: color 0.3s 0.1s ease, border 0.3s 0.1s ease; }
      .cmn__btn:hover, .cmn__btn:focus {
        color: #9c150c;
        border: 0.3rem solid rgba(255, 255, 255, 0.5); }
      .cmn__btn:active {
        color: #9c150c;
        border: 0.3rem solid rgba(255, 255, 255, 0.5); } }
  .cmn__btn--maroon {
    color: #9c150c;
    border: 0.3rem solid #9c150c;
    transition: border 0.3s 0.1s ease; }
    @media (min-width: 768px) {
      .cmn__btn--maroon:hover, .cmn__btn--maroon:focus {
        border: 0.3rem solid rgba(156, 21, 12, 0.5); }
      .cmn__btn--maroon:active {
        border: 0.3rem solid rgba(156, 21, 12, 0.5); } }
  @media (max-width: 1140px) {
    .cmn__btn {
      font-size: 1.6rem;
      line-height: 2rem;
      padding: 1.2rem 3rem; } }
  @media (max-width: 768px) {
    .cmn__btn {
      font-size: 1.4rem;
      line-height: 1.8rem;
      padding: 1rem 2rem; } }

.cmn__nav-link {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2rem;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #ffffff; }
  .cmn__nav-link--black {
    color: #000000; }
    .cmn__nav-link--black.active {
      font-weight: 500; }
  @media (max-width: 768px) {
    .cmn__nav-link {
      font-size: 1.4rem;
      line-height: 1.6rem; } }

